    }
    .analyze-fans .analyze-statistics .card-list .card-item:nth-child(2) {
        left: 15rem;
        top: 0rem;
        width: 228px;
    }
    .analyze-fans .analyze-statistics .card-list .card-item:nth-child(3) {
        left: 1rem;
        top: 9rem;
        width: 319px;
    }
    .analyze-fans .analyze-statistics .card-list .card-item:nth-child(4) {
        left: 16rem;
        top: 11rem;
        width: 293px;
    }
}

@media (max-width: 768px) {
    .analyze-fans .analyze-statistics .card-list {
        height: 450px;
        transform: scale(0.65);
    }
    .analyze-fans .analyze-statistics .card-list .card-item:nth-child(1) {
        left: -2rem;
        top: 0rem;
        width: 200px;
    }
    .analyze-fans .analyze-statistics .card-list .card-item:nth-child(2) {
        left: 12rem;
        top: 0rem;
        width: 200px;
    }
    .analyze-fans .analyze-statistics .card-list .card-item:nth-child(3) {
        left: -1rem;
        top: 8rem;
        width: 279px;
    }
    .analyze-fans .analyze-statistics .card-list .card-item:nth-child(4) {
        left: 13rem;
        top: 10rem;
        width: 256px;
    }
}

@media (max-width: 480px) {
    .analyze-fans .analyze-statistics .card-list {
        height: 400px;
        transform: scale(0.95);
    }
    .analyze-fans .analyze-statistics .card-list .card-item:nth-child(1) {
        left: -3rem;
        top: 0rem;
        width: 171px;
    }
    .analyze-fans .analyze-statistics .card-list .card-item:nth-child(2) {
        left: 8rem;
        top: 0rem;
        width: 171px;
    }
    .analyze-fans .analyze-statistics .card-list .card-item:nth-child(3) {
        left: -2rem;
        top: 7rem;
        width: 239px;
    }
    .analyze-fans .analyze-statistics .card-list .card-item:nth-child(4) {
        left: 12rem;
        top: 7rem;
        width: 220px;
    }
}

@keyframes afTopLayer {
    0% { transform: translate(0px, 0px) skewX(12deg); }
    25% { transform: translate(15px, -15px) skewX(12deg); }
    50% { transform: translate(25px, 10px) skewX(12deg); }
    75% { transform: translate(-15px, -5px) skewX(12deg); }
    100% { transform: translate(0px, 0px) skewX(12deg); }
}
